@import "../variables.scss";

.p1 { 
  display: inline-block;
  margin: 120px 0; 


  .right {
    @include text;
    line-height: 21px;
    width: 50%;
  }
  .toptitle {
    width: 60%;
  }
  .Link {
    color: $black;
    text-decoration: underline;
    margin-right: 3.2px;
}

  .Link:hover {
    color: $yellow;
  }
   
  .header {
    font-size: 28px;
    margin-top: 0;
    margin-bottom: 0px;
    font-weight: 600;
    color: $indigo;
    letter-spacing: 0.8px;
    text-align: left;
  }
}

.categorytitle {
  @include uppergraytext;
  font-size: 25px;
  color: $indigo;
  margin-bottom: 20px;
  font-weight: 600;
}
.categorytext {
  margin-bottom: 20px;
}

.sensorimg {
  width: 70%;
  margin-top: 5%;
  max-width: 300px;
  position: relative;
  display: flex;
  float: right;

  @media only screen and (max-width: 767px) {
      margin-top: 50px;
  }

  @media only screen and (max-width: 600px) {
      display: block;
      width: 70%;
      margin: 20px auto;
      margin-bottom: 100px;
  }

  .sensor1, .sensor2, .sensor3 {
      position: absolute;
      display: block;
      left: 0;
      transition: 1.2s all;
      width: 100%;
      user-select: none;

      &:hover {
          transition: 1.2s all;
      }
  }

  .sensor1 {
      z-index: 3; 
      top: calc(50%);
      transform: translateY(-110%);

      &:hover {
          transform: translateY(calc(-55px - 110%));
      }

      @media only screen and (max-width: 600px) {
          top: 0;
          transform: translateY(10%);
          &:hover {
              transform: translateY(calc(-55px + 10%));
          }
      }    
  }

  .sensor2 {
      z-index: 2;
      top: 50%;
      transform: translateY(-50%);

      &:hover {
          transform: translateY(calc(-55px - 50%));
      }

      @media only screen and (max-width: 600px) {
          top: 0;
          transform: translateY(50%);
          &:hover {
              transform: translateY(calc(-55px + 50%));
          }
      }    
  }

  .sensor3 {
      z-index: 1;
      top: 50%;
      transform: translateY(10%);

      &:hover {
          transform: translateY(calc(-55px + 10%));
      }

      @media only screen and (max-width: 600px) {
          top: 0;
          transform: translateY(95%);
          &:hover {
              transform: translateY(calc(-55px + 95%));
          }
      }    
  }    
}

.subtitle {
  @include uppergraytext;
  margin-bottom: 100px;
}

.category {
  @include uppergraytext;
  letter-spacing: 1.5px;
  margin-bottom: 12px;
}

.categoryindigo {
  @include uppergraytext;
  letter-spacing: 1.5px;
  margin-bottom: 12px;
  color: $indigo;
}

.bodytext {
  @include text;
}

.featuretext {
  margin-top: 160px;
  @include text;
  font-size: 24px;
  letter-spacing: -0.1px;
  line-height: 45px;
}

.bodytext a {
  @include text;
  color: $indigo;
  transition: 0.5s;
  text-decoration: none;
}

.bodytext a:hover {
  color: $gray;
}

.imgcaption {
  text-align: center;
  font-size: 16px;
  font-weight: 600;
  font-style: italic;
}

.greybg {
  background-color: #fcfcfc;
  padding: 35px 45px;
}
.indigobg {
  background-color: #F3F0FB;
  padding: 35px 45px;
}

.categoryscreen {
  @include uppergraytext;
  letter-spacing: 1.5px;
  margin-top: 0px;
  text-align: center;
  margin-bottom: -20px;
}
.twocolumn {
  display: flex;
  flex-direction: row;
  margin-top: 50px;

  @media only screen and (max-width: 600px) {
      display: block;
      text-align: center;
  }
  .left1 {
    width: 60%;
    float: left;
  }
  .right1 {
    width: 30%;
    float: right;
    margin-left: 0px;
    margin-right: 50px;
    margin-top: 12%;

  }
  .left2 {
    width: 30%;
    float: left;
    margin-top: 10%;
    margin-left: 50px;
  }
  .right2 {
    width: 60%;
    float: right;
    margin-left: 0px;
    margin-right: 50px;
  }
}


.underlined-button {
  color: $black;
  text-decoration: none;
  font-family: 'Karla';
  font-size: 13px;
  font-weight: 600;
  transition: 0.3s filter;
  position: relative;
  margin-bottom: 50px;
  text-align: left;
}

.underlined-button:hover {
  filter: brightness(105%);
  cursor: pointer;
}

.underlined-button:hover::after {
  width: 100%;
}

.underlined-button:after {
  content: "";
  position: absolute;
  height: 2px;
  left: 0;
  bottom: -7px;
  background: $yellow;
  width: 15px;
  border-radius: 10%;
  transition: 0.3s width;
}



